.main-bg{
  height: 300px;
  border: bold;
  background-image: url('./img/modeling.jpg');
  background-size: cover;
  border: bold;
  background-position: center center;
  height: 500px;
  background-repeat: no-repeat;
  margin-bottom: 50px;
}
.main-bg > .title{
  color:white;
  font-size: 5em;
}
.main-bg > .recent{
  display: flex;
  position:relative;
  width: 50%;
  height: 15em;
  text-align: start;
  color:white;
  font-size: 1em;
  overflow-x: hidden;
  overflow-y:auto;
  background:transparent;
}
.img-main{
/* width: 100px; */
margin: 10px;
border-radius:10%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav-main{
  list-style: none;
  color:black;
  margin: 5px;
  align-items: center;
}

/* Detail.js css */
.div-main{
  margin: 100px;
}
.div-sub{
  margin: 15px;
}

.btn-add{
  margin: 10px;
  background: black;
  color: white;
  border: rgb(0, 0, 0) 5px solid;
}
.start{
  transform: scale(0);
}
.end{
  transform: scale(1);
  transition: all 0.5s;
}
.ms-auto{
  color: white;
}